import React, { useRef, useEffect } from "react"
import * as d3 from "d3"

export function DrawXAxis(svgRef, innerRadius, outerRadius) {

	const yaxis = d3.select(svgRef.current).select("g.yaxis");
	
  	const y = d3.scaleLinear()
    .domain([1, 10])
    .range([innerRadius, outerRadius])	

    yaxis.attr("text-anchor", "middle")
    .attr("font-family", "sans-serif")
    .attr("font-size", 10)
    .call(g => g.selectAll("g")
      .data(y.ticks(5).reverse())
      .join("g")
        .attr("fill", "none")
        .call(g => g.append("circle")
            .attr("stroke", "#000")
            .attr("stroke-opacity", 0.2)
            .attr("r", y))
        .call(g => g.append("text")
            .attr("y", d => y(d))
            .attr("dy", "0.35em")
            .attr("stroke", "#fff")
            .attr("stroke-width", 5)
            .text((x, i) => `${x.toFixed(0)}`)
          .clone(true)
            .attr("y", d => y(d))
          .selectAll(function() { return [this, this.previousSibling]; })
          .clone(true)
            .attr("fill", "currentColor")
            .attr("stroke", "none")))
}

export function DrawYAxis(svgRef, innerRadius, outerRadius) {

	const xaxis = d3.select(svgRef.current).select("g.xaxis");
	
	const x = d3.scaleLinear()
  	.domain([0,7])
    .range([0, 2 * Math.PI]);
	
	const header = [
		'Planning',
		'Competence',
		'Performance',
		'Wellbeing',
		'Contract',
		'Legal',
		'Targets'
	]
	
	xaxis.attr("font-family", "sans-serif")
    .attr("font-size", 10)
    .call(g => g.selectAll("g")
      .data(x.ticks(7))
      .join("g")
        .call(g => g.append("path")
            .attr("stroke", "#000")
            .attr("stroke-opacity", 0.2)
            .attr("id", d => `line-${d}`)
            .attr("d", d => `
              M${d3.pointRadial(x(d), innerRadius)}
              L${d3.pointRadial(x(d), outerRadius)}
            `))
        .call(g => g.append("text")
        	.attr("text-anchor", "left")
        	.attr("stroke-opacity", 0.2)
          .append("textPath")
            .attr("stroke", "#fff")
            .attr("stroke-width", 5)
        	.attr("rotate","180")
            .attr("href", d =>  `#line-${d}`)
            .text(d => header[d])
          .clone(true)
            .attr("fill", "currentColor")
            .attr("stroke", "none")))
}


export function DrawData(svgRef, innerRadius, outerRadius) {

	const gdata = d3.select(svgRef.current).select("g.data");
	
  	const y = d3.scaleLinear()
    .domain([1, 10])
    .range([innerRadius, outerRadius])	

	const x = d3.scaleLinear()
  	.domain([0,7])
    .range([0, 2 * Math.PI]);
	
	const area = d3.areaRadial()
    .curve(d3.curveLinearClosed)
    .angle(d => x(d.topic))
    
	const line = d3.lineRadial()
    .curve(d3.curveLinearClosed)
    .angle(d => x(d.topic))
    
	
	const data = [
  	{topic: 0, avg: 8.5, min: 8.0, max: 9.0, minmin: 1.0, maxmax: 10},
  	{topic: 1, avg: 6.8, min: 3.0, max: 9.0, minmin: 1.0, maxmax: 10},
  	{topic: 2, avg: 7.5, min: 6.5, max: 7.8, minmin: 1.0, maxmax: 10},
  	{topic: 3, avg: 8.2, min: 6.0, max: 9.0, minmin: 1.0, maxmax: 10},
  	{topic: 4, avg: 7.3, min: 6.5, max: 7.4, minmin: 0, maxmax: 0},
  	{topic: 5, avg: 9.0, min: 8.5, max: 9.5, minmin: 0, maxmax: 0},
  	{topic: 6, avg: 8.9, min: 8.0, max: 9.2, minmin: 0, maxmax: 0},
  	{topic: 7, avg: 8.5, min: 8.0, max: 9.0, minmin: 1.0, maxmax: 10}
  	];
	
	/*
	gdata.append("path")
      .attr("fill", "lightsteelblue")
      .attr("fill-opacity", 0.2)
      .attr("d", area
          .innerRadius(d => y(d.minmin))
          .outerRadius(d => y(d.maxmax))
        (data));
    */

	gdata.append("path")
/*      .attr("fill", "steelblue")*/
      .attr("fill", "lightsteelblue")
      .attr("fill-opacity", 0.2)
      .attr("d", area
          .innerRadius(d => y(d.min))
          .outerRadius(d => y(d.max))
        (data));
        
  	gdata.append("path")
      .attr("fill", "none")
      .attr("stroke", "steelblue")
      .attr("stroke-width", 1.5)
      .attr("d", line
          .radius(d => y(d.avg))
        (data));
}


