import React, { useRef, useEffect } from "react"
import { Container } from "react-bootstrap"
import * as d3 from "d3"


export default function Report() {

  const svgRef = useRef(null);
  
  const data = [
  	{topic: 1, avg: 7.0, min: 6.0, max: 8.0, minmin: 1.0, maxmax: 10},
  	{topic: 2, avg: 6.5, min: 5.9, max: 7.0, minmin: 1.0, maxmax: 10},
  	{topic: 3, avg: 7.3, min: 6.5, max: 7.8, minmin: 1.0, maxmax: 10},
  	{topic: 4, avg: 7.2, min: 6.1, max: 7.8, minmin: 1.0, maxmax: 10},
  	{topic: 5, avg: 0, min: 0, max: 0, minmin: 0, maxmax: 0},
  	{topic: 6, avg: 0, min: 0, max: 0.0, minmin: 0, maxmax: 0},
  	{topic: 7, avg: 0, min: 0, max: 0.0, minmin: 0, maxmax: 0},
  	{topic: 8, avg: 7.0, min: 6.0, max: 8.0, minmin: 1.0, maxmax: 10}
  ];
  
  const header = [
  	'',
  	'johtamisen tavoitteet',
  	'Henkilöstösuunnittelu',
  	'Osaamisen johtaminen',
  	'Suorituksen johtaminen',
  	'',
  	'',
  	'',
  	'johtamisen tavoitteet',
  ]

  const x = d3.scaleLinear()
  	.domain([1,8])
    .range([0, 2 * Math.PI]);

  const innerRadius = 100;
  const outerRadius = 200;

  const y = d3.scaleLinear()
    .domain([1, 10])
    .range([innerRadius, outerRadius])

  const area = d3.areaRadial()
    .curve(d3.curveLinearClosed)
    .angle(d => x(d.topic))
    
  const line = d3.lineRadial()
    .curve(d3.curveLinearClosed)
    .angle(d => x(d.topic))

  useEffect(() => {
  	const svg = d3.select(svgRef.current)
  		.attr("font-family", "sans-serif")
    	.attr("font-size", 10);
    	
    const gdata = svg.select("g.data");
    
    const gaxis = svg.select("g.axis");
    	
    gdata.append("path")
      .attr("fill", "lightsteelblue")
      .attr("fill-opacity", 0.2)
      .attr("d", area
          .innerRadius(d => y(d.minmin))
          .outerRadius(d => y(d.maxmax))
        (data));

	gdata.append("path")
      .attr("fill", "steelblue")
      .attr("fill-opacity", 0.2)
      .attr("d", area
          .innerRadius(d => y(d.min))
          .outerRadius(d => y(d.max))
        (data));
        
  	gdata.append("path")
      .attr("fill", "none")
      .attr("stroke", "steelblue")
      .attr("stroke-width", 1.5)
      .attr("d", line
          .radius(d => y(d.avg))
        (data));
        
    svg.attr("text-anchor", "middle")
    .attr("font-family", "sans-serif")
    .attr("font-size", 10)
    .call(g => g.selectAll("g")
      .data(y.ticks().reverse())
      .join("g")
        .attr("fill", "none")
        .call(g => g.append("circle")
            .attr("stroke", "#000")
            .attr("stroke-opacity", 0.2)
            .attr("r", y))
        .call(g => g.append("text")
            .attr("y", d => y(d))
            .attr("dy", "0.35em")
            .attr("stroke", "#fff")
            .attr("stroke-width", 5)
            .text((x, i) => `${x.toFixed(0)}`)
          .clone(true)
            .attr("y", d => y(d))
          .selectAll(function() { return [this, this.previousSibling]; })
          .clone(true)
            .attr("fill", "currentColor")
            .attr("stroke", "none")))

	svg.attr("font-family", "sans-serif")
    .attr("font-size", 10)
    .call(g => g.selectAll("g")
      .data(x.ticks(8))
      .join("g")
        .call(g => g.append("path")
            .attr("stroke", "#000")
            .attr("stroke-opacity", 0.2)
            .attr("id", d => `line-${d}`)
            .attr("d", d => `
              M${d3.pointRadial(x(d), innerRadius)}
              L${d3.pointRadial(x(d), outerRadius)}
            `))
        .call(g => g.append("text")
        	.attr("text-anchor", "right")
        	.attr("text-align", "right")
        	.attr("stroke-opacity", 0.2)
          .append("textPath")
            .attr("startOffset", 50)
            .attr("stroke", "#fff")
            .attr("stroke-width", 5)
            .attr("href", d =>  `#line-${d}`)
            .text(d => header[d])
          .clone(true)
            .attr("fill", "currentColor")
            .attr("stroke", "none")))
         
  });

	return (
		<Container>
			<div className="py-5 text-center">
			<img className="d-block mx-auto mb-4" src="/Sponsor_Logo.jpg" alt="" width="161" height="51" />
			<h2>Report</h2>
			<svg viewBox="-210,-210,420,420" strokeLinejoin="round" strokeLinecap="round" width={420} height={420} ref={svgRef} style={{ background: "white", margin: "10px" }} >
				<g className="axis"/>
				<g className="data" />
      		</svg>
      		</div>
		</Container>
	);
}